
body {
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
main {
  text-align: center;
  margin-top: 40px;
}
header {
  margin: 0;
  height: 56px;
  padding: 0 16px 0 24px;
  background-color: #35495E;
  color: #ffffff;
}
header span {
  display: block;
  position: relative;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 16px;
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.center {
  margin: 1rem auto;
  width: 100%;
}
.menu {
  background: #ecf0f1;
  height: 100vh;
}
.gameStarted {
  background: #fff;
}
.fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
  position: absolute;
}
.fade-enter-active {
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
.body-disabled {
  pointer-events: none;
}

.btn {
  border: none;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background: none;
  cursor: pointer;
  padding: 25px 80px;
  display: inline-block;
  margin: 15px 30px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 700;
  outline: none;
  position: relative;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border-radius: 5px;
  color: #fff;
  width: 15rem;
}
.btn.active {
    -webkit-box-shadow: unset;
            box-shadow: unset;
    top: unset;
    background-color: #fff;
    color: #000;
    border: 1px solid;
}
.btn:after {
  content: '';
  position: absolute;
  z-index: -1;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
  /* Button 1 */
.btn-red {
  background: #cb4e4e;
  -webkit-box-shadow: 0 6px #ab3c3c;
          box-shadow: 0 6px #ab3c3c;
}
.btn-red:active {
    -webkit-box-shadow: 0 0 #ab3c3c;
            box-shadow: 0 0 #ab3c3c;
    top: 6px;
}
.btn-red:hover {
  -webkit-box-shadow: 0 8px #ab3c3c;
          box-shadow: 0 8px #ab3c3c;
  top: -2px;
}
